@charset "UTF-8";
/* Font style setting */
$score:"s-core", Arial, Apple SD Gothic Neo, sans-serif;

/* Point color setting */
$black:#000;
$white:#fff;

$red:#ef4247;

$gray1:#282e30;

/* Input - color setting */
$inp-bd-base:$gray1;

/* Button - color setting */
$btn-black:$black;

/* BASE SETTING */
$base-font:"Gordita", Arial, Apple SD Gothic Neo, sans-serif;
$base-font-size:22px;
$base-font-color:$gray1;
$letter-spacing:0;
$light:300;
$medium:500;
$bold:600;
$base-font-weight:$medium;

