@charset "UTF-8";
html { height:100%; }
body { height:100%; overflow:hidden; margin:0; padding:0; font-family:Gordita, sans-serif; }
h1 { margin:0; padding:0; display:inline-block; }
li { list-style:none; }
a { text-decoration:none; }
button { padding:0; border:0; outline:none; cursor:pointer; font-family:Gordita, sans-serif; font-weight:500; }
.gnb_container { width:74%; height:50px; border-bottom:1px solid #c4c5c6; }
.gnb_container .gnb_wrap { max-width:1680px; margin:0 auto; padding:5px 0 0 80px; }
.gnb_container .gnb_wrap .logo{ width:74px; height:37px; display:inline-block; background:url(../../images/layout/logo.png) no-repeat; }
.gnb_container .gnb_wrap .webtour_btn { float:right; padding:8px; margin:2px 50px 0 0; color:#e0236b; font-size:15px; font-weight:600; border: 2px solid #e0236b; border-radius:20px; }

.user_info { margin:10px auto 0 50px; position:absolute; top:50%; right:50%; transform:translate(623px, -400px); }
.user_email, .connect_time { font-size:18px; font-weight:600; }
.select_video { width:calc(100% - 500px); position:absolute; top:52%; transform:translateY(-50%); margin:0; text-align:center; }
.select_video .title { text-align: left; margin:10px auto 0 50px; position:absolute; top:50%; left:50%; transform:translate(-671px, 360px); }
.select_video .title_01 { width:40px; height:10px; margin:0 0 8px 0; padding:1px 8px; border-radius:6px; color:$white; font-size:20px; font-weight:600; background-color:#a4a5a6; }
.select_video .title_02 { font-size:19.6px; font-weight:600; }

.video_list { width:500px; height:100%; position:absolute; top:0; right:0; background-color:#f8f8f8; }
.video_list .tab_tv { width:250px; padding:15px 20px; background-color:#f8f8f8; font-size:20px; font-weight:600; }
.video_list .tab .active { background:linear-gradient(170deg, #a70cba 0%, #e40d4a 100%); color:#fff; }
.video_list .tab_av { width:250px; padding:15px 20px; float:right; background-color:#f8f8f8; font-size:20px; font-weight:600; }

.video_list .tab_tv_contents { height:calc(100% - 50px); background-color:#f8f8f8; overflow-y:scroll; }
.video_list .tab_tv_contents li:first-child { margin:25px 0 0 0; }
.video_list .tab_tv_contents li { width:100%; cursor:pointer; position:relative; }
.video_list .tab_tv_contents li.active:before { width:8px; height:100%; position:absolute; display:block; content:''; background-color:#e0236b;}
.video_list .tab_tv_contents li .contents_wrap { padding:8px 21px; box-sizing:border-box; }
.video_list .tab_tv_contents li.active .contents_wrap { background-color:#e3e4e5; }
.video_list .tab_tv_contents li img { width:132px; height:74px; background-size:cover; }
.video_list .tab_tv_contents li a { width:350px; height:80px; display:block; }
.video_list .tab_tv_contents li a:after { display:block; content:''; clear:both; }
.video_list .tab_tv_contents .title { width:290px; float:right; }
.video_list .tab_tv_contents .title_01 { display:inline-block; margin:0 0 8px 0; padding:2px 4px; letter-spacing:0.5px; border-radius:2px; color:$white; font-size:13px; font-weight:600; background-color:#a4a5a6; }
.video_list .tab_tv_contents .title_02 { display:block; font-size:16px; font-weight:600; clear:both; }

.video_list .tab_av_contents { height:calc(100% - 50px); background-color:#f8f8f8; overflow-y:scroll; }
.video_list .tab_av_contents li:first-child { margin:25px 0 0 0; }
.video_list .tab_av_contents li { width:100%; cursor:pointer; position:relative; }
.video_list .tab_av_contents li.active:before { width:8px; height:100%; position:absolute; display:block; content:''; background-color:#e0236b;}
.video_list .tab_av_contents li .contents_wrap { padding:8px 21px; box-sizing:border-box; }
.video_list .tab_av_contents li.active .contents_wrap { background-color:#e3e4e5; }
.video_list .tab_av_contents li img { width:132px; height:74px; }
.video_list .tab_av_contents li a { width:350px; height:80px; display:block; }
.video_list .tab_av_contents li a:after { display:block; content:''; clear:both; }
.video_list .tab_av_contents .title { width:290px; float:right; }
.video_list .tab_av_contents .title_01 { display:inline-block; margin:0 0 8px 0; padding:2px 4px; letter-spacing:0.5px; border-radius:2px; color:$white; font-size:13px; font-weight:600; background-color:#a4a5a6; }
.video_list .tab_av_contents .title_02 { display:block; font-size:16px; font-weight:600; clear:both; }

.description { padding:15px 0 0; margin:0 50px 0 0; font-size:12px; float:right; position:absolute; top:50%; left:50%; transform:translate(175px, 360px); }